<template>
<div>
  <img class="wave__image_1" :style="waveStyleFirst" src="../../assets/wave_1.png"></img>
  <img class="wave__image_2" :style="waveStyleSecond" src="../../assets/wave_2.png"></img>
</div>
</template>

<script>
const OPACITY_DAMP_1 = 800;
const OPACITY_DAMP_2 = 1200;
const START_SHOW_1 = 100;
const START_SHOW_2 = 500;
const TRANSLATE_Y_CONST_1 = 3;
const TRANSLATE_Y_CONST_2 = 4;
const MAX_WIDTH_SCREEN = 1400;
const DAMP_START_POSITION_1 = 10;
const DAMP_START_POSITION_2 = 300;

export default {
  props: {
    scrolled: Number,
  },
  computed: {
    waveStyleFirst() {
      return {
        transform: `
        translateY(${(this.scrolled / TRANSLATE_Y_CONST_1) + ((MAX_WIDTH_SCREEN - window.innerWidth) / DAMP_START_POSITION_1)}px)`,
        opacity: `${Math.min(Math.max(this.scrolled - START_SHOW_1, 150) / OPACITY_DAMP_1, 0.9)}`,
      };
    },
    waveStyleSecond() {
      return {
        transform: `
        translateY(${(this.scrolled / TRANSLATE_Y_CONST_2) + ((MAX_WIDTH_SCREEN - window.innerWidth) / DAMP_START_POSITION_2)}px)`,
        opacity: `${Math.min(Math.max(this.scrolled - START_SHOW_2, 150) / OPACITY_DAMP_2, 0.9)}`,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.wave__image_1 {
  position: absolute;
  width: calc(100px + 50%);
  max-width: 800px;
  bottom: -50px;
  left: -5px;
  z-index: 0;
  object-fit: contain;
}
.wave__image_2 {
  position: absolute;
  width: calc(20% + 100px);
  max-width: 400px;
  bottom: -450px;
  left: calc(30% + 50px);
  z-index: 0;
  object-fit: contain;
}
</style>



