<template>
  <div id="app" :class="containerClass">
    <section-navigation :colorClass="navClass"></section-navigation>
    <story-synopsis ref="synopsis" :scrolled="scrolled" :active="containerClass === 'on--synopsis'"></story-synopsis>
    <cast-list ref="casts" :scrolled="scrolled" :active="containerClass === 'on--casts'"></cast-list>
    <sponsors-list ref="sponsors" :scrolled="scrolled" :active="containerClass === 'on--sponsors'"></sponsors-list>
    <ticket-page ref="tickets" :scrolled="scrolled" :active="containerClass === 'on--tickets'"></ticket-page>
    <buy-ticket :active="containerClass !== 'on--tickets'"></buy-ticket>
  </div>
</template>

<script>
import SectionNavigation from '@/components/SectionNavigation';
import StorySynopsis from '@/components/StorySynopsis';
import CastList from '@/components/CastList';
import SponsorsList from '@/components/SponsorsList';
import BuyTicket from '@/components/BuyTicket';
import TicketPage from '@/components/BuyTicket/TicketPage';

const PX_BEFORE_CHANGE = 400;

export default {
  name: 'app',
  components: {
    SectionNavigation,
    StorySynopsis,
    CastList,
    SponsorsList,
    BuyTicket,
    TicketPage,
  },
  data() {
    return {
      scrolled: 0,
      containerClass: 'on--synopsis',
      navClass: 'on--synopsis',
    };
  },
  watch: {
    scrolled(val) {
      switch (true) {
        case val > this.$refs.tickets.$el.offsetTop - PX_BEFORE_CHANGE:
          this.setContainerClass('on--tickets');
          this.setNavClass('on--tickets');
          break;
        case val > this.$refs.sponsors.$el.offsetTop - PX_BEFORE_CHANGE:
          this.setContainerClass('on--sponsors');
          this.setNavClass('on--sponsors');
          break;
        case val > this.$refs.casts.$el.offsetTop - PX_BEFORE_CHANGE:
          this.setContainerClass('on--casts');
          this.setNavClass('on--casts');
          break;
        default:
          this.setContainerClass('on--synopsis');
          this.setNavClass('on--synopsis');
      }
    },
  },
  methods: {
    updateScrolled() {
      this.scrolled = window.pageYOffset;
    },
    setContainerClass(className) {
      this.containerClass = className;
    },
    setNavClass(className) {
      this.navClass = className;
    },
  },
  mounted() {
    document.addEventListener('scroll', this.updateScrolled);
  },
};
</script>

<style lang="scss">
$black: #333;
$lightGrey: #EEE;
$offWhite: #FCFCFC;

body {
  margin: 0;
  background: $lightGrey;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  transition: all 1s ease;
  &.on--synopsis {
    background: $black;
  }
  &.on--casts {
    background: $lightGrey;
  }
  &.on--sponsors {
    background: white;
  }
  &.on--tickets {
    background: #232222;
  }
}
.roboto--slab {
  font-family: 'Roboto Slab', serif;
}
</style>



