<template>
  <div class="navigation-container" :class="colorClass">
    <!-- <div class="icnIcon__wrapper"><div class="icn__icon"></div></div> -->
    <div class="navigation__wrapper">
      <div class="navigation__item" v-scroll-to="'#synopsis'">Synopsis</div>
      <div class="navigation__item" v-scroll-to="'#casts'">Casts</div>
      <div class="navigation__item" v-scroll-to="'#sponsors'">Sponsors</div>
      <div class="navigation__item" v-scroll-to="'#tickets'">Tickets</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    colorClass: String,
  },
};
</script>

<style lang="scss" scoped>
.navigation-container {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(75px + 5vw);
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 10;
  .icnIcon__wrapper {
    position: fixed;
    width: 80px;
    height: 30px;
    background: rgba(255,255,255,.8);
    top: 0;
    right: 30px;
    padding: 10px 0;
    transition: all .5s ease;
    box-shadow: 0 5px 20px -5px rgba(0,0,0,.1);
    .icn__icon {
      height: 100%;
      width: 100%;
      background-image: url('../assets/icn-logo_bnw.png');
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  .navigation__wrapper {
    border-right-width: 2px;
    border-right-style: solid;
    cursor: pointer;
    padding: 40px 0 40px 0;
    .navigation__item {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 120px;
      height: 50px;
      transition: color .1s ease, box-shadow .2s ease;
      &:hover {
        transition: color .1s ease, box-shadow .1s ease;
      }
    }
  }
  &.on--synopsis {
    color: #FFF;
    border-right-color: #888;
    .navigation__item:hover {
      box-shadow: -30px 0 60px -40px rgb(255,255,255) inset;
    }
    .icnIcon__wrapper {
      opacity: 0;
      top: -50px;
    }
  }
  &.on--casts {
    color: #333;
    border-right-color: #333;
    .navigation__item:hover {
      box-shadow: -30px 0 60px -40px rgb(119, 33, 11) inset;
    }
  }
  &.on--sponsors {
    color: #333;
    border-right-color: #333;
    .navigation__item:hover {
      box-shadow: -30px 0 60px -40px rgb(0,0,0) inset;
    }
  }
  &.on--tickets {
    color: #FFF;
    border-right-color: #FFF;
    .navigation__item:hover {
      box-shadow: -30px 0 60px -40px rgb(255,255,255) inset;
    }
  }
}

@media screen and (max-width: 1000px) {
	.navigation-container {
		display: none;
	}
}
</style>



