<template>
  <div class="card__container" :class="cardClass"
    @mousedown="doSelect(idx)" v-scroll-to="'#casts'">
    <div class="card__photo" :style="cardStyle"></div>
    <div class="photoOverlay" :class="{ show: selected }"></div>
    <div class="card__desc">
      <div class="character roboto--slab">{{ detail.name }}</div>
      <div class="actor">{{ detail.actor }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    selected: Boolean,
    doSelect: Function,
    idx: Number,
    somethingSelected: Boolean,
    detail: Object,
  },
  computed: {
    cardClass() {
      return {
        somethingSelected: this.somethingSelected,
        selected: this.selected,
      };
    },
    cardStyle() {
      return {
        'background-image': `url(${this.detail.photoUrl})`,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.card__container {
  position: relative;
  flex-grow: 1;
  background: #FFF;
  text-align: left;
  width: 12.5%;
  min-width: 175px;
  height: calc(60vh - 100px);
  margin: 7.5px;
  background: #AAA;
  box-shadow: 0 15px 50px -20px rgba(0,0,0,.3);
  transition: transform 1s ease, margin-top .4s ease, height .4s ease, width .4s ease, box-shadow .2s ease;
  overflow: hidden;
  border-radius: 4px;
  &:hover {
    margin-top: -5px;
    box-shadow: 0 20px 50px -15px rgba(119, 33, 11, .5);
    cursor: pointer;
  }
  &.somethingSelected {
    transform: translateY(400px);
    height: 300px;
    width: 7.5%;
  }
  &.selected {
    box-shadow: 0 15px 50px -20px rgba(0,0,0,.3);
  }
  .card__photo {
    width: 100%;
    height: calc(100% - 75px);
    margin-top: -2px;
    background-size: cover;
    background-position-x: 50%;
  }
  .card__desc {
    position: absolute;
    padding: 10px 20px;
    background: white;
    width: 100%;
    bottom: 0;
    .character {
      font-weight: 500;
      font-size: 1.75em;
      color: #77210b;
    }
    .actor {
      color: #888;
    }
  }
  .photoOverlay {
    opacity: 0;
    background: linear-gradient(10deg, rgba(119, 33, 11, 1) -5%, rgba(119, 33, 11, 0) 70%);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity .2s ease;
    &.show {
      opacity: 1;
    }
  }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to {
  opacity: 0
}
</style>



