<template>
<div class="sponsorsPage__container" id="sponsors">
  <div class="sponsors__container">
    <h2 class="sponsors__title roboto--slab" :class="{ hidden: !active }">Sponsors</h2>
    <div class="sponsors" :class="{ hidden: !active }">
      <img class="sponsors__logo logo--narrow" src="../assets/sponsors/main/shopee.png"></img>
      <img class="sponsors__logo logo--wide" src="../assets/sponsors/main/bank-indonesia.png"></img>
      <img class="sponsors__logo logo--wide" src="../assets/sponsors/main/wardaya-college.png"></img>
    </div>
    <h2 class="sponsors__title roboto--slab" :class="{ hidden: !active }">Official Partner</h2>
    <div class="officialPartner" :class="{ hidden: !active }">
      <img class="op__logo logo--normal" src="../assets/sponsors/official-partner/escape-hunt.png"></img>
      <img class="op__logo logo--narrow" src="../assets/sponsors/official-partner/kanekin.png"></img>
      <img class="op__logo logo--normal" src="../assets/sponsors/official-partner/official-hotel-partner.png"></img>
      <img class="op__logo logo--normal" src="../assets/sponsors/official-partner/official-printing-partner.png"></img>
    </div>
    <h2 class="sponsors__title roboto--slab" :class="{ hidden: !active }">Supported By</h2>
    <div class="supportedBy" :class="{ hidden: !active }">
      <img class="support__logo logo--narrow" src="../assets/sponsors/supported-by/kbri.png"></img>
      <img class="support__logo logo--narrow" src="../assets/sponsors/supported-by/Lambang_Kemdikbud.png"></img>
      <img class="support__logo logo--narrow" src="../assets/sponsors/supported-by/rumah_budaya.png"></img>
      <img class="support__logo logo--wide" src="../assets/sponsors/supported-by/ppis.png"></img>
    </div>
    <h2 class="sponsors__title roboto--slab" :class="{ hidden: !active }">Media Partner</h2>
    <div class="mediaPartner" :class="{ hidden: !active }">
      <img class="media__logo logo--wide" src="../assets/sponsors/media-partner/kanekin.png"></img>
    </div>
  </div>
</div>
</template>

<script>
export default {
  props: {
    active: Boolean,
  },
};
</script>

<style lang="scss" scoped>
.sponsorsPage__container {
  width: calc(100vw - 60px);
  min-height: 100vh;
  padding: 30px;
  .sponsors__title {
    margin-top: 70px;
    transition: opacity .3s ease;
    &.hidden {
      opacity: 0;
      transition: opacity .5s ease;
    }
  }
  .sponsors__container {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    .sponsors         { transition: opacity .5s ease .4s; }
    .officialPartner  { transition: opacity .5s ease .5s; }
    .supportedBy      { transition: opacity .5s ease .6s; }
    .mediaPartner     { transition: opacity .5s ease .7s; }
    .sponsors, .officialPartner, .supportedBy, .mediaPartner {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      &.hidden {
        opacity: 0;
        transition: opacity .2s ease;
      }
      .sponsors__logo {
        margin: 30px 40px;
        font-size: 5rem;
        width: 80%;
      }
      .op__logo {
        margin: 40px;
        font-size: 4rem;
        width: 60%;
      }
      .support__logo {
        margin: 40px;
        font-size: 4rem;
        width: 60%;
      }
      .media__logo {
        margin: 40px;
        font-size: 5rem;
        width: 60%;
      }
      .logo--narrow {
        max-width: 3em;
      }
      .logo--wide {
        max-width: 4em;
      }
      .logo--normal{
        max-width: 3.5em;
      }
    }
  }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .75s ease .25s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>



