<template>
  <transition name="fade">
    <div class="ticket__container" v-if="active">
      <div class="ticket__location">Drama Centre Theatre - National Library Building</div>
      <div class="ticket__date">3 February 2018</div>
      <div class="ticket__time">2.30 PM &amp; 7.30 PM</div>
      <a><div class="ticket__link">Buy Tickets</div></a>
      <a><div class="ticket__faq">FAQ</div></a>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    active: Boolean,
  },
};
</script>

<style lang="scss" scoped>
.ticket__container {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 75px;
  background: white;
  box-shadow: 0 0 50px -10px rgba(0,0,0,.1);
  z-index: 10;
  div {
    margin: 15px;
  }
  .ticket__link {
    padding: 10px 20px;
    border: 3px solid #77210b;
    font-weight: 500;
    color: #77210b;
    transition: all .5s ease;
    user-select: none;
    &:hover {
      cursor: pointer;
      background: #77210b;
      color: white;
      transition: all .1s ease;
    }
    &:active {
      background-color: #59180c;
      border-color: #59180c;
    }
  }
  .ticket__faq {
    color: #77210b;
    font-weight: 500;
    user-select: none;
    &:hover {
      text-decoration: underline;
      cursor: pointer;
    }
  }
  @media screen and (max-width: 900px) {
    .ticket__location, .ticket__date, .ticket__time {
      display: none;
    }
  }
}

.fade-enter-active {
  transition: all .2s ease;
}
.fade-leave-active {
  transition: transform .5s ease, opacity .2s ease .1s;
}
.fade-enter, .fade-leave-to {
  transform: translateY(75px);
  opacity: 0;
}
</style>



