<template>
  <div class="casts__container" id="casts">
    <transition name="slide-fade">
      <div class="casts__actorPhoto" :style="photoStyle" v-if="selectedActor !== -1">
        <div class="photoOverlay"></div>
        <transition name="slide">
          <div class="slidingNewPhoto" v-if="changingPhoto.isChanging" :style="slidingPhotoStyle"></div>
        </transition>
      </div>
    </transition>
    <div class="casts__description" :class="{ hidden: !active, somethingSelected: selectedActor !== -1  }">
      <div class="casts__title">
        <h1 class="roboto--slab">Casts</h1>
      </div>
      <div class="actor__desc__wrapper" :class="{ hidden: selectedActor === -1, changing: changingPhoto.isChanging }">
        <div class="actor__desc">
          <div class="desc__character roboto--slab">{{ details[shownSelectedPhoto].name }}</div>
          <div class="desc__actor">{{ details[shownSelectedPhoto].actor }}</div>
          <p>{{ details[shownSelectedPhoto].description }}</p>
        </div>
      </div>
    </div>
    <div class="casts__wrapper" ref="wrapper" :class="{ hidden: !active }" :style="wrapperStyle">
      <div class="innerWrapper">
        <cast-card v-for="(detail, idx) in details.slice(0,3)" :key="idx" :selected="selectedActor === idx"
        :doSelect="selectActor" :idx="idx" :somethingSelected="selectedActor !== -1" :detail="details[idx]">
        </cast-card>
      </div>
      <div class="innerWrapper">
        <cast-card v-for="(detail, idx) in details.slice(3,6)" :key="idx + 3" :selected="selectedActor === idx + 3"
        :doSelect="selectActor" :idx="idx + 3" :somethingSelected="selectedActor !== -1" :detail="details[idx + 3]">
        </cast-card>
      </div>
    </div>
  </div>
</template>

<script>
import CastCard from './CastCard';

const DAMP_SCROLL_CARD = 5;
const DAMP_SCROLL_PHOTO = 10;
const START_PARALLAX = 1200;

export default {
  props: {
    scrolled: Number,
    active: Boolean,
  },
  components: {
    CastCard,
  },
  data() {
    return {
      timeoutId: null,
      selectedActor: -1,
      changingPhoto: {
        isChanging: false,
        newPhotoUrl: '',
      },
      shownSelectedPhoto: 0,
      details: [
        {
          name: 'Ambe',
          actor: 'Leonardo Donni Moelyanto',
          photoUrl: '/static/lakonna/doni.jpg',
          description: `
          Ambe has long been the sworn leader of his peaceful village up in the mountains of Tana Toraja.
          Leading with remarkable wisdom,he guided the village into great prosperity. As a father, Ambe loved both of his sons dearly.
          But, having spent most of his life maintaining the village, he seemed to be a better village leader than he was a father for Arrang and Bayo.`,
        },
        {
          name: 'Arrang',
          actor: 'Krismanto Liman',
          photoUrl: '/static/lakonna/manto.jpg',
          description: `Arrang was the oldest son of the village leader.
          He was a charming man who was deeply admired by the people for his prowess as a fighter.
          With his impeccable fighting skills and his remarkable leadership abilities,
          Arrang was more than ready to lead the village and embrace his destiny.
          However, when Arrang first laid eyes on Tanoana, the love of his life, another future awaited him. Another destiny.`,
        },
        {
          name: 'Bayo',
          actor: 'Billy Kurniawan',
          photoUrl: '/static/lakonna/billy.jpg',
          description: `Bayo was the youngest son of the village leader. As a child, Bayo had a kind-hearted and good-willed soul.
          But as Bayo grew older, he began to notice that he was constantly being compared with his older brother.
          This made Bayo believe that his peers loved Arrang more than him. Wanting to prove his worth and be respected
          by the people around him – including his own father–, Bayo dreamed to be known for his own name.`,
        },
        {
          name: 'Malitta',
          actor: 'Clarita Saslim',
          photoUrl: '/static/lakonna/lala.jpg',
          description: `As the primadonna of the village, Malitta was one of the most endeared members of her community.
          She was not only an ambitious woman, but also a very calculated intellect. Since long, she has loved Arrang,
          her childhood friend, constantly dreaming to be with him. But when she discovered that Arrang’s heart is with someone else,
          there's no telling what she would do to get him back.`,
        },
        {
          name: 'Tinuwu',
          actor: 'Fransisca Andriani',
          photoUrl: '/static/lakonna/sisca.jpg',
          description: `Tinuwu was a cheerful girl who deeply loved the village, dreaming that everyone who lived there would be happy.
          Being a close friend of Bayo since childhood, Tinuwu understood him-- and his false thoughts, really well.
          She would always try to cheer him up during his moments of distress and hopelessness, hoping that he could see that his life was worth much more than he thought.`,
        },
        {
          name: 'Tanoana',
          actor: 'Josefanny Amadea Gunawan',
          photoUrl: '/static/lakonna/fanny.jpg',
          description: `Losing the family she loved at an early age, Tanoana had to face life on her own.
          Eventually, she grew up to be a strong and independent woman. To sustain herself, she made a living by becoming a dancer.
          She had long desired to experience love, but the grim reality had given her no time for such endeavours.
          Never once did she then have the thought of pursuing dreams and joys with a loved one until she met Arrang.`,
        },
      ],
    };
  },
  watch: {
    active() {
      this.selectedActor = -1;
    },
  },
  computed: {
    photoStyle() {
      const wrapper = this.$refs.wrapper;
      const wrapperPos = wrapper ? wrapper.offsetTop : 0;
      return {
        'background-image': this.selectedActor !== -1 ?
        `url(${this.details[this.shownSelectedPhoto].photoUrl})` : 'none',
        transform: `translateY(${(this.scrolled - wrapperPos - START_PARALLAX) / DAMP_SCROLL_PHOTO}px)`,
      };
    },
    wrapperStyle() {
      const wrapper = this.$refs.wrapper;
      const wrapperPos = wrapper ? wrapper.offsetTop : 0;
      return {
        transform: `translateY(${-(this.scrolled - wrapperPos - START_PARALLAX) / DAMP_SCROLL_CARD}px)`,
      };
    },
    slidingPhotoStyle() {
      return { 'background-image': `url(${this.changingPhoto.newPhotoUrl})` };
    },
  },
  methods: {
    selectActor(idx) {
      this.selectedActor = idx;
      if (idx !== this.shownSelectedPhoto) {
        this.changingPhoto.isChanging = true;
        this.changingPhoto.newPhotoUrl = this.details[idx].photoUrl;
      }
      clearTimeout(this.timeoutId);
      this.timeoutId = setTimeout(() => {
        this.shownSelectedPhoto = idx;
        this.changingPhoto.isChanging = false;
      }, 600);
    },
  },
};
</script>

<style lang="scss" scoped>
.casts__container {
  position: relative;
  width: 100vw;
  min-height: calc(100vh - 100px);
  transition: opacity .25s ease .2s, left .5s ease .25s;
  .casts__description {
    position: absolute;
    margin: 0;
    left: calc(45% - 40px);
    top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    transition: opacity .25s ease, left .8s ease-in-out;
    .casts__title {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: font-size .5s ease .25s;
      width: 150px;
      height: 100px;
      padding: 20px 0 10px 40px;
      margin-left: -50px;
      color: #232222;
      background-image: url('../../assets/casts_title_bg.png');
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      h1 {
        color: white;
        margin: 0;
        text-shadow: 0 5px 15px black;
      }
    }
    .actor__desc__wrapper {
      width: 500px;
      transition: all .2s ease;
      background: white;
      box-shadow: 10px 20px 40px -10px rgba(0,0,0,.2);
      &.hidden {
        opacity: 0;
      }
      &.changing .actor__desc {
        opacity: 0;
        transform: translateX(20px);
      }
      .actor__desc {
        transition: opacity .2s ease .2s, transform .5s ease;
        padding: 50px;
        text-align: left;
        .desc__character {
          font-size: 3rem;
          font-weight: 500;
          color: #77210b;
        }
        .desc__actor {
          font-size: 1.5rem;
          color: #888;
        }
      }
    }
    &.somethingSelected .casts__title {
      font-size: 0.8rem;
      color: #333;
    }
    &.hidden {
      opacity: 0;
    }
  }
  .casts__wrapper {
    width: calc(90% - 200px);
    padding: 180px 100px 50px 100px;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    transition: opacity .5s ease .5s;
    z-index: 2;
    &.hidden {
      transition: opacity .25s ease .2s;
      opacity: 0;
    }
    .innerWrapper {
      display: flex;
      flex-grow: 1;
      flex-wrap: wrap;
    }
  }
  .casts__actorPhoto {
    position: absolute;
    overflow: hidden;
    right: 0;
    top: 40px;
    width: 40%;
    height: calc(90vh - 50px);
    z-index: 0;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 0 30px 70px -15px rgba(0,0,0,.5);
    .photoOverlay {
      width: 100%;
      height: 100%;
      background: linear-gradient(15deg, black -5%, rgba(0,0,0,0) 70%)
    }
    .slidingNewPhoto {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
  @media screen and (max-width: 1000px) {
    .casts__actorPhoto {
      width: 75%;
      right: calc(60px + 1vw);
    }
    .casts__description {
      left: calc(40px - 3vw);
      .casts__title {
        transform: translateX(-10px);
      }
      .actor__desc__wrapper {
        background: rgba(255,255,255,.95);
        width: 85vw;
      }
      &.somethingSelected {
        .casts__title {
          color: white;
          text-shadow: 5px 5px 15px rgba(0,0,0,.3);
        }
      }
    }
    .casts__wrapper {
      padding: 400px 50px 0 50px;
      width: calc(100% - 100px);
    }
  }
  @media screen and (min-width: 2160px) {
    .casts__description.somethingSelected {
      top: calc(20vh - 40px);
      left: 50vw;
    }
    .casts__wrapper {
      padding: 35vh 25px 0 25px;
    }
  }
}

.slide-fade-enter-active {
  transition: all .3s ease-out .1s;
}
.slide-fade-leave-active {
  transition: all .5s ease-in .2s;
}
.slide-fade-enter, .slide-fade-leave-to {
  transform: translateX(100px);
  opacity: 0;
}

.slide-enter-active {
  transition: all .6s cubic-bezier(.5,0,.22,1);
}
.slide-leave-active {
  transition: opacity .5s ease;
}
.slide-enter {
  transform: translateX(100%);
  opacity: .5;
}
.slide-leave-to {
  opacity: 0;
}
</style>



