<template>
<div class="synopsis--container" id="synopsis">
<div class="synopsis__bg" :class="{ 'bg--hidden': !active }" :style="bgStyle"></div>
  <div class="synopsis--wrapper-tb-cell">
    <div class="synopsis--wrapper-flex" :class="{ 'wrapper--hidden': !active }">
      <div class="synopsis--content">
        <h1 class="synopsis__title roboto--slab">Synopsis</h1>
        <div class="synopsis__text">
          <p>
            Inspired by a renowned Torajan folktale ‘Batingna Lebonna’, 
            Lakonna is a story about choice. It is a tale of pursuit; a hunt for glory; a search for happiness.</p>
          <p>
            Centuries ago, among the mountains that ornamented Tana Toraja, stood a village under the ruling of its wise leader.
            The leader had two sons whom he loved dearly. The firstborn, Arrang, was a charming man admired by not only the villagers,
            but also by Malitta, his long-time friend. Witnessing Arrang’s success, the younger son of the two, Bayo,
            felt unloved by the people who often compared him to his older brother. Despite his insecurities, he had Tinuwu,
            his childhood friend who always tried to cheer him up.</p>
          <p>
            As time went by, the village leader grew old and his body weakened.
            As a result, Arrang was chosen to take his father’s place in leading the village.
            His life soared to even greater heights when he met the love of his life, Tanoana.
            Their devoted love for each other was so great that they made a vow to always be together in life and in death.
            It seemed as if Arrang’s life couldn’t be any more perfect.
            However, with jealousy looming in the shadows, an ill-fated choice would change the course of his life, and of the people around him, forever.</p>
        </div>
      </div>
      <div class="lakonnaLogo" :style="logoStyle"></div>
    </div>
  </div>
  <animated-wave :scrolled="scrolled"></animated-wave>
  <div class="icnLogo" :style="icnLogoStyle"></div>
</div>
</template>

<script>
import AnimatedWave from '@/components/StorySynopsis/AnimatedWave';

const LOGO_DAMP_SCROLL_TRANSLATE = 5;
const ICN_DAMP_SCROLL_TRANSLATE = 2.5;
const PATTERN_DAMP_SCROLL = 1.5;

export default {
  components: {
    AnimatedWave,
  },
  props: {
    scrolled: Number,
    active: Boolean,
  },
  data() {
    return {};
  },
  computed: {
    logoStyle() {
      return {
        transform: `translateY(${this.scrolled / LOGO_DAMP_SCROLL_TRANSLATE}px)`,
      };
    },
    bgStyle() {
      return {
        'background-position-y': `${this.scrolled / PATTERN_DAMP_SCROLL}px`,
      };
    },
    icnLogoStyle() {
      return {
        transform: `translateY(${this.scrolled / ICN_DAMP_SCROLL_TRANSLATE}px)`,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.synopsis--container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: table;
  .synopsis--wrapper-tb-cell {
    vertical-align: middle;
    display: table-cell;
  }
  .synopsis--wrapper-flex {
    display: flex;
    flex-wrap: wrap-reverse;
    justify-content: center;
    align-items: center;
    transition: opacity .5s ease .5s;
    &.wrapper--hidden {
      opacity: 0;
      transition: opacity .25s ease;
    }
  }
  .synopsis__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../assets/pattern-lakonna.png');
    background-repeat: repeat-x;
    background-size: 75%;
    transition: opacity .5s ease .75s;
    &.bg--hidden {
      opacity: 0;
      transition: opacity .25s ease;
    }
  }
}
.synopsis--content {
  max-width: 750px;
  padding: 100px;
  padding-top: 0;
  padding-left: 150px;
  color: white;
  text-align: left;
  z-index: 1;
  .synopsis__title {
    font-size: 42px;
  }
}
.lakonnaLogo {
  margin: 30px;
  width: 400px;
  height: 600px;
  background: url('../../assets/lakona-web_logo.png');
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
.icnLogo {
  position: absolute;
  top: 45px;
  left: calc(50vw - 50px);
  background-image: url('../../assets/icn-logo_white.png');
  width: 100px;
  height: 100px;
  z-index: 2;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
@media screen and (max-width: 1450px){
  .icnLogo {
    width: 80px;
    height: 80px;
    right: 40px;
    top: 35px;
    left: unset;
  }
}
@media screen and (max-width: 600px) {
  .synopsis--content {
    padding: 0 40px;
    .synopsis__title {
      margin-top: 0;
      font-size: 60px;
    }
  }
  .lakonnaLogo {
    width: 250px;
    margin-bottom: 0;
  }
  .icnLogo {
    width: 60px;
    height: 60px;
    top: 35px;
    left: calc(50vw - 30px);
  }
}
</style>



