<template>
  <div class="ticket__page" id="tickets" ref="ticketPage">
    <div class="poster__wrapper">
      <div class="poster__bg" :class="{ hidden: !active }" :style="posterBgStyle"></div>
      <transition name="fade">
        <div class="poster" :style="perspectiveStyle" v-if="active">
          <img class="bg__wave" src="../../assets/tickets/ticket-wave-bg.png"></img>
          <img class="donny" src="../../assets/tickets/donny_poster.png"></img>
          <img class="lala" src="../../assets/tickets/lala_poster.png"></img>
          <img class="billy" src="../../assets/tickets/billy_poster.png"></img>
          <img class="krismanto" src="../../assets/tickets/krismanto_poster.png"></img>
          <img class="sisca" src="../../assets/tickets/sisca_poster.png"></img>
          <img class="fanny" src="../../assets/tickets/fanny_poster.png"></img>
        </div>
      </transition>
    </div>
    <div class="ticket__info" :class="{ hidden: !active }">
      <img class="info__logo" src="../../assets/lakona-web_logo.png"></img>
      <div class="info__content">
        <h5 class="roboto--slab">VENUE</h5>
        <div class="info__value">Drama Centre Theatre<br>National Library Building</div>
        <h5 class="roboto--slab">DATE</h5>
        <div class="info__value">3 February 2018</div>
        <h5 class="roboto--slab">TIME</h5>
        <div class="info__value">2.30 PM &amp; 7.30 PM</div>
        <a><div class="ticket__link">Buy Tickets</div></a>
      </div>
    </div>
  </div>
</template>

<script>
const DAMP_PERSPECTIVE_Y = 5;
const DAMP_TRANSLATE_BG = 4;
const PARALLAX_OFFSET = 150;

export default {
  props: {
    scrolled: Number,
    active: Boolean,
  },
  computed: {
    perspectiveStyle() {
      const { ticketPage } = this.$refs;
      const pagePos = ticketPage ? ticketPage.offsetTop : 0;
      return {
        'perspective-origin': `50% ${100 - ((pagePos - this.scrolled) / DAMP_PERSPECTIVE_Y)}%`,
      };
    },
    posterBgStyle() {
      const { ticketPage } = this.$refs;
      const pagePos = ticketPage ? ticketPage.offsetTop : 0;
      return {
        transform: `translateY(${(this.scrolled - pagePos - PARALLAX_OFFSET) / DAMP_TRANSLATE_BG}px)`,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.ticket__page {
  width: calc(100vw - 150px);
  min-height: calc(100vh - 50px);
  padding: 0 50px 50px 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  .poster__wrapper {
    flex-grow: 3;
    position: relative;
    width: 60%;
    height: calc(100vh - 75px);
    min-height: calc(50vw);
    padding-top: 25px;
    margin-left: 80px;
    max-height: 50vw;
    overflow: hidden;
    .poster__bg {
      position: absolute;
      top: 40px;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url('../../assets/tickets/bg_poster.jpg');
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      opacity: .6;
      transition: opacity 1s ease .2s;
      &.hidden {
        opacity: 0;
        transition: opacity 1s;
      }
    }
    .poster {
      width: 100%;
      height: 100%;
      perspective: 1400px;
      transform-style: preserve-3d;
      .bg__wave {
        position: absolute;
        left: -2%;
        top: 10%;
        width: 102%;
        transform: rotateX(20deg);
      }
      .donny {
        position: absolute;
        left: -31%;
        top: 4%;
        width: 65%;
        transform: translateZ(-50px);
      }
      .lala {
        position: absolute;
        left: 7.5%;
        top: calc(40% - 100px);
        width: 24%;
        transform: translateZ(30px);
      }
      .billy {
        position: absolute;
        left: 27.5%;
        top: 18%;
        width: 28%;
        transform: translateZ(-25px);
      }
      .krismanto {
        position: absolute;
        left: 40%;
        top: calc(35% - 80px);
        width: 30%;
        transform: translateZ(25px);
      }
      .fanny {
        position: absolute;
        left: 65%;
        top: calc(80% - 250px);
        width: 27.5%;
        transform: translateZ(60px);
      }
      .sisca {
        position: absolute;
        left: 73%;
        top: calc(50% - 190px);
        width: 27.5%;
        transform: translateZ(-5px);
      }
    }
  }
  .ticket__info {
    flex-grow: 1;
    min-width: 300px;
    min-height: calc(100% - 200px);
    margin: 60px;
    margin-bottom: 25px;
    box-shadow: 0 20px 75px -10px rgba(0,0,0,.1);
    background: linear-gradient(10deg, #505050, #333 50%);
    transition: opacity .5s ease .3s;
    display: flex;
    flex-direction: column;
    padding: 30px;
    align-items: center;
    justify-content: center;
    &.hidden {
      opacity: 0;
      transition: opacity .2s;
    }
    .info__logo {
      width: 150px;
      margin: 20px;
    }
    .info__content {
      width: 100%;
      h5 {
        color: #888;
        padding-top: 20px;
      }
      .info__value {
        color: white;
        font-size: 1.2rem;
      }
      .ticket__link {
        margin: 80px 0;
        padding: 10px 20px;
        border: 3px solid #FAFAFA;
        font-weight: 500;
        color: #FAFAFA;
        transition: all .5s ease;
        user-select: none;
        &:hover {
          cursor: pointer;
          background: #9e2a2a;
          border: 3px solid #9e2a2a;
          color: white;
          transition: all .1s ease;
        }
        &:active {
          background-color: #59180c;
          border-color: #59180c;
        }
      }
    }
  }
  @media screen and (max-width: 1400px) {
    .poster__wrapper .poster {
      perspective: none;
      transform-style: unset;
    }
  }
  @media screen and (max-width: 1000px) {
    padding: 25px;
    width: calc(100vw - 50px);
    height: unset;
    padding-bottom: 0;
    .poster__wrapper{
      display: none;
    }
    .ticket__info {
      margin: 0;
    }
  }
}
.fade-enter-active {
  transition: all .1s ease-out;
}
.fade-leave-active {
  transition: all .5s ease-in .2s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>



